import React, { useCallback } from 'react'; //  useRef
import { useSwipeGesture } from '../../hooks/useSwipeGesture';
import { useTouchFeedback } from '../../hooks/useTouchFeedback';

const SwipeableCard = ({
  children,
  onSwipeLeft,
  onSwipeRight,
  onSwipeUp,
  onSwipeDown,
  onTap,
  className = '',
  style: customStyle = {},
  threshold = 60,
  disabled = false
}) => {
  // ---  1:  elementRef ---
  const {
    getTransform,
    getOpacity,
    isDragging,
    elementRef 
  } = useSwipeGesture({
    onSwipeLeft,
    onSwipeRight,
    onSwipeUp,
    onSwipeDown,
    threshold
  });

  const {
    style: feedbackStyle,
    getEventHandlers
  } = useTouchFeedback({
    scale: 0.98,
    duration: 200
  });

  const handleCardClick = useCallback((e) => {
    if (isDragging) {
      e.preventDefault();
      e.stopPropagation();
      return;
    }
    if (onTap) {
      onTap();
    }
  }, [isDragging, onTap]);

  const mergedStyle = {
    ...customStyle,
    ...feedbackStyle,
    transform: isDragging ? getTransform() : (customStyle.transform || undefined),
    opacity: isDragging ? getOpacity() : (customStyle.opacity || undefined),
    cursor: 'pointer',
    touchAction: 'none', // ---  2:  pan-y  none ---
    userSelect: 'none',
    WebkitUserSelect: 'none',
    transition: isDragging ? 'none' : 'all 0.5s ease-out' // 
  };

  const eventHandlers = getEventHandlers();

  return (
    <div
      // ---  3:  elementRef  cardRef ---
      ref={elementRef} 
      className={className}
      style={mergedStyle}
      onClick={handleCardClick}
      role="button"
      tabIndex={0}
      onMouseDown={eventHandlers.onMouseDown}
      onMouseUp={eventHandlers.onMouseUp}
      onMouseLeave={eventHandlers.onMouseLeave}
      onTouchStart={eventHandlers.onTouchStart}
      onTouchEnd={eventHandlers.onTouchEnd}
      onTouchCancel={eventHandlers.onTouchCancel}
      onKeyDown={eventHandlers.onKeyDown}
      onKeyUp={eventHandlers.onKeyUp}
    >
      {children}
    </div>
  );
};

export default SwipeableCard;